import { request } from 'utils';

const prefix = '/api/categories';

export const getPointLadderFours = async () =>
  request({
    prefix,
    url: '/StudyClassificationIn4',
    params: {},
    method: 'GET',
  });

export const getPointLadderFourById = async id =>
  request({
    prefix,
    url: `/StudyClassificationIn4/${id}`,
    params: {},
    method: 'GET',
  });

export const createPointLadderFour = async values =>
  request({
    prefix,
    url: '/StudyClassificationIn4',
    params: {},
    data: { ...values },
    method: 'POST',
  });

export const updatePointLadderFour = async values =>
  request({
    prefix,
    url: `/StudyClassificationIn4/${values.id}`,
    params: {},
    data: { ...values },
    method: 'PUT',
  });

export const deletePointLadderFourById = async id =>
  request({
    prefix,
    url: `/StudyClassificationIn4/${id}`,
    params: {},
    method: 'DELETE',
  });



