import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, message } from 'antd';
import { requiredRule } from 'utils/validateRules';
import {
  getPointLadderFourById,
  createPointLadderFour,
  updatePointLadderFour,
} from 'services/pointLadderFour';

@Form.create({ name: 'FormPointLadderFour' })
class FormPointLadderFour extends Component {
  state = {
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    const { id } = match.params;
    if (id) {
      this.getPointLadderFourById(id);
    }
  };

  getPointLadderFourById = async id => {
    const res = await getPointLadderFourById(id);
    this.setState({ initialValue: res.data });
  };

  savePointLadderFour = async pointLadderFour => {
    const { id } = this.state.initialValue;
    pointLadderFour.id = id;
    try {
      if (!id) {
        await createPointLadderFour(pointLadderFour);
      } else {
        await updatePointLadderFour(pointLadderFour);
      }
      message.success('Lưu thành công');
      this.props.history.goBack();
    } catch (err) {
      message.error('Lưu không thành công');
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.savePointLadderFour(values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card
        title={initialValue.id ? 'Cập nhật xếp loại thang điểm 4' : 'Thêm xếp loại thang điểm 4'}
      >
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Xếp loại" {...formItemLayout}>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
              {getFieldDecorator('labelInEnglish', {
                initialValue: initialValue.labelInEnglish,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm" hasFeedback>
              {getFieldDecorator('fromGrade', {
                initialValue: initialValue.fromGrade,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến điểm" hasFeedback>
              {getFieldDecorator('toGrade', {
                initialValue: initialValue.toGrade,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormPointLadderFour {...props} />
  </Page>
));



